import React, { useState } from 'react';
import OperationsCard from './component/OperationsCard';
import Hesapmakinesi from './img/HesapMakinesi.png';
import NotHesapla from './img/NotHesapla.png';
import DikdrtgenAlanCevreHesaplama from './img/DikdrtgenAlanCevreHesaplama.png';
import KareAlanCevreHesaplama from './img/KareAlanCevreHesaplama.png';
import DaireAlanCevreHesaplama from './img/DaireAlanCevreHesaplama.png';
import EbobEkok from './img/EbobEkok.png';
import UcgenAlanCevreHesaplama from './img/genAlanCevreHesaplama.png';
import BirimCevirici from './img/BirimCevirici.png';
import MathModal from './component/MathModal';
import BasicCalculator from './modal/BasicCalculator';
import GradeCalculator from './modal/GradeCalculator';
import RectangleCalculator from './modal/RectangleCalculator';
import SquareCalculator from './modal/SquareCalculator';
import CircleCalculator from './modal/CircleCalculator';
import TriangleCalculator from './modal/TriangleCalculator ';
import GCDAndLCMCalculator from './modal/GCDAndLCMCalculator';
import UnitConverter from './modal/UnitConverter';
import TipsCard from './component/TipsCard';
import TermCard from './component/TermsCard';

const Home = () => {
  const [isModalOpen, setModalOpen] = useState(false);
  const [modalContent, setModalContent] = useState({ title: '', body: null });

  const openModal = (title, body) => {
    setModalContent({ title, body });
    setModalOpen(true);

  };

  const closeModal = () => {
    setModalOpen(false);
  };

  return (
    <div>
      <main>
        <div id='operations' className='my-4'>
          <div className="container mx-auto my-6 text-start text-[#6c757d]">
            <h2 className="text-4xl font-bold mb-6">lemler</h2>
          </div>

          <div className="overflow-x-auto scrollbar-hide mx-0">
            <div className="flex justify-center">
              <div className="grid grid-cols-2 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6 px-4">
                <OperationsCard
                  imageSrc={Hesapmakinesi}
                  title={"Temel Hesap Makinesi"}
                  description={'Basit matematiksel ilemler iin kullanabileceiniz bu hesap makinesi ile toplama, karma, arpma ve blme ilemlerini kolayca yapabilirsiniz.'}
                  onClick={() => openModal("Temel Hesap Makinesi", <BasicCalculator />
                  )}
                />
                <OperationsCard
                  imageSrc={NotHesapla}
                  title={"Not Hesaplama Arac"}
                  description={'Aldnz notlar girerek ortalamanz hzlca hesaplamanza yardmc olur.'}
                  onClick={() => openModal("Not Hesaplama Arac", <GradeCalculator />)}
                />
                <OperationsCard
                  imageSrc={DikdrtgenAlanCevreHesaplama}
                  title={"Dikdrtgen Alan ve evre Hesaplama"}
                  description={'Dikdrtgenin alann ve evresini hzlca hesaplayarak pratik matematiksel ilemler yapmanz salar.'}
                  onClick={() => openModal("Dikdrtgen Hesaplama", <RectangleCalculator />)}
                />
                <OperationsCard
                  imageSrc={KareAlanCevreHesaplama}
                  title={"Kare Alan ve evre Hesaplama"}
                  description={'Kare eklinin alann ve evresini kolayca hesaplayarak hzl zmler sunar.'}
                  onClick={() => openModal("Kare Hesaplama", <SquareCalculator />)}
                />
                <OperationsCard
                  imageSrc={DaireAlanCevreHesaplama}
                  title={"Daire Alan ve evre Hesaplama"}
                  description={'Dairenin alann ve evresini hesaplayarak dairesel ekillerle ilgili ilemlerinizi kolaylatrr.'}
                  onClick={() => openModal("Daire Hesaplama", <CircleCalculator />)}
                />
                <OperationsCard
                  imageSrc={UcgenAlanCevreHesaplama}
                  title={"gen Alan ve evre Hesaplama"}
                  description={'genin taban ve ykseklik deerleri ile alann hzlca hesaplamanz salar. Matematiksel ilemlerinizi kolaylatrr.'}
                  onClick={() => openModal("gen Hesaplama", <TriangleCalculator />)}
                />
                <OperationsCard
                  imageSrc={EbobEkok}
                  title={"Ebob Ekok Hesaplama"}
                  description={'Saylarn en byk ortak bleni (EBOB) ve en kk ortak kat (EKOK) hesaplamalarn kolayca yapabilirsiniz.'}
                  onClick={() => openModal("Ebob Ekok Hesaplama", <GCDAndLCMCalculator />)}
                />
                <OperationsCard
                  imageSrc={BirimCevirici}
                  title={"Birim evirici"}
                  description={'Farkl l birimlerini hzlca evirerek pratik hesaplamalar yapmanza olanak tanr. lmler aras geileri kolaylatrr.'}
                  onClick={() => openModal("Birim evirici", <UnitConverter />)}
                />
              </div>
            </div>
          </div>
        </div>
        <MathModal isOpen={isModalOpen} onClose={closeModal} content={modalContent} />
        <div id='Tips' className='my-4'>
          <div className="container mx-auto my-6 text-start text-[#6c757d]">
            <h2 className="text-4xl font-bold mb-6">pular</h2>
          </div>
          <div className="grid grid-cols-2 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6 px-4">
            <TipsCard title={"Dik genin zellikleri"} description={"Dik genin en nemli zellii, bir asnn 90 derece olmasdr. Pisagor teoremi ile kenar uzunluklar arasnda iliki kurabilirsiniz: a^2 + b^2 = c^2."} />
            <TipsCard title={"Oran ve Orant"} description={"Oran, iki saynn karlatrlmasdr. Orant ise iki oran arasndaki eitliktir. Problemleri zmek iin oranlar kullanarak, bilinmeyen deerleri bulabilirsiniz."} />
            <TipsCard title={"arpanlar ve Katlar"} description={"Bir saynn arpanlar, o sayy tam blen saylardr. Katlar ise bir saynn 1 ile arpm sonulardr. Bu kavramlar, saylar arasndaki ilikileri anlamanza yardmc olur."} />
            <TipsCard title={"Blme lemi"} description={"Blme ilemi, bir sayy (pay) baka bir sayya (payda) blmektir. Kalan ile birlikte yazmak, tam say ve kesirli saylarn anlalmasn kolaylatrr."} />
            <TipsCard title={"Kesirler"} description={"Kesirler, bir btnn parasn temsil eder. Pay ve payda arasndaki iliki, kesirin deerini belirler. Kesirleri toplarken ve karrken ortak payda bulmak nemlidir."} />
            <TipsCard title={"Pozitif ve Negatif Saylar"} description={"Pozitif saylar 0'dan byktr, negatif saylar ise 0'dan kktr. Bu iki grup arasndaki ilemler, iaret kurallarna gre yaplr."} />
            <TipsCard title={"arpanlara Ayrma"} description={"Polinomlar arpanlarna ayrmak, denklemleri zmede ve daha karmak matematiksel ilemleri basitletirmede nemlidir."} />
            <TipsCard title={"EBOB ve EKOK"} description={"En byk ortak blen (EBOB) ve en kk ortak kat (EKOK), iki saynn blenleri ve katlar arasndaki ilikileri belirler. Bu kavramlar kullanarak kesirleri sadeletirebilirsiniz."} />
            <TipsCard title={"lem ncelii"} description={"Matematiksel ilemlerde ilem ncelii, parantezler, arpma ve blme, toplama ve karma eklindedir. Bu sraya dikkat etmek doru sonular elde etmenizi salar."} />
            <TipsCard title={"sl Saylar"} description={"sl saylar, bir saynn kendisiyle arplma saysn ifade eder. rnein, 2^3 = 2 x 2 x 2 = 8. sl saylarla ilem yaparken kurallara dikkat edin."} />
            <TipsCard title={"Fonksiyonlar"} description={"Fonksiyon, bir girdi (x) ile bir kt (y) arasnda belirli bir iliki tanmlar. Fonksiyonlar anlamak, matematiksel dnme becerisini gelitirir."} />
            <TipsCard title={"Grafikler"} description={"Matematikte grafikler, fonksiyonlarn ve verilerin grselletirilmesine yardmc olur. Grafikleri okuyabilmek, fonksiyonlarn davranlarn anlamanz salar."} />
            <TipsCard title={"Yzde Hesaplama"} description={"Yzde, bir saynn 100'e blnmesiyle elde edilen bir orandr. Yzde hesaplamalarnda, orantlar ve kesirler nemli rol oynar."} />
            <TipsCard title={"Denklemler"} description={"Denklem, eitlik iindeki bilinmeyenleri belirlemek iin kullanlr. Basit denklemleri zmek, daha karmak matematik problemlerini anlamanza yardmc olur."} />
            <TipsCard title={"Veri Analizi"} description={"Verilerin analizi, ortalama, medyan ve mod gibi istatistiksel kavramlarn anlalmasn salar. Bu, verilerin daha iyi yorumlanmasna yardmc olur."} />
            <TipsCard title={"Geometrik ekiller"} description={"Geometrik ekillerin zelliklerini bilmek, alan ve evre hesaplamalar yapabilmenizi salar. Dikdrtgen, kare, daire gibi ekillerin formllerini renin."} />
            <TipsCard title={"Kmeler"} description={"Kmeler, belirli bir zellie sahip nesneler topluluudur. Kme teorisi, matematiksel mantk ve problemlerin zmnde nemlidir."} />
            <TipsCard title={"Mantksal Dnme"} description={"Mantksal dnme, problem zme becerilerini gelitirir. Matematikte mantk, karm yapma ve analiz etme konusunda yardmc olur."} />
            <TipsCard title={"Say Dizileri"} description={"Say dizileri, belirli bir kurala gre sralanm saylardr. Fibonacci dizisi gibi dizileri anlamak, matematiksel ilikileri kefetmenize yardmc olur."} />
            <TipsCard title={"Zihin Haritalar"} description={"Matematik kavramlarn zihin haritalar ile grselletirmek, renmeyi ve bilgiyi organize etmeyi kolaylatrr. Zihin haritalar, ilikileri ve balantlar anlamanz salar."} />
            </div>
        </div>
        <div id='Terms' className='my-4'>  
        <div className="container mx-auto my-6 text-start text-[#6c757d]">
            <h2 className="text-4xl font-bold mb-6">Terimler</h2>
          </div>
          <div className="grid grid-cols-2 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6 px-4">
          <TermCard term={"Dik gen"} definition={"Dik gen, bir asnn 90 derece olduu gendir. Pisagor teoremi ile tannr."} />
        <TermCard term={"Fonksiyon"} definition={"Fonksiyon, bir girdi (x) ile bir kt (y) arasnda belirli bir iliki tanmlar."} />
        <TermCard term={"Kesir"} definition={"Kesir, bir btnn parasn temsil eder; pay ve payda ile tanmlanr."} />
        <TermCard term={"Oran"} definition={"Oran, iki saynn karlatrlmasdr; orant ise iki oran arasndaki eitliktir."} />
        <TermCard term={"arpan"} definition={"arpan, bir sayy tam blen saydr. rnein, 2 ve 3, 6'nn arpanlardr."} />
        <TermCard term={"Blme"} definition={"Blme, bir sayy (pay) baka bir sayya (payda) blme ilemidir."} />
        <TermCard term={"EBOB"} definition={"En Byk Ortak Blgen, iki veya daha fazla saynn ortak blenlerinin en bydr."} />
        <TermCard term={"EKOK"} definition={"En Kk Ortak Kat, iki veya daha fazla saynn ortak katlarnn en kdr."} />
        <TermCard term={"sl Saylar"} definition={"sl saylar, bir saynn kendisiyle arplma saysn ifade eder. rnein, 2^3 = 2 x 2 x 2."} />
        <TermCard term={"Veri Analizi"} definition={"Veri analizi, istatistiksel kavramlarn anlalmasn salar; ortalama, medyan ve mod gibi."} />
        <TermCard term={"Geometrik ekiller"} definition={"Geometrik ekiller, belirli kurallara gre tanmlanan dzlemsel veya uzaysal nesnelerdir."} />
        <TermCard term={"Denklem"} definition={"Denklem, bir eitlik iindeki bilinmeyenleri belirlemek iin kullanlr."} />
        <TermCard term={"Diziler"} definition={"Diziler, belirli bir kurala gre sralanm say veya terimler topluluudur."} />
        <TermCard term={"Kmeler"} definition={"Kmeler, belirli bir zellie sahip nesnelerin topluluudur; kme teorisi ile tannr."} />
        <TermCard term={"Mantksal Dnme"} definition={"Mantksal dnme, problem zme becerilerini gelitirir; karm ve analiz yapma becerisidir."} />
        <TermCard term={"Kkl Saylar"} definition={"Kkl saylar, bir saynn karekk veya kp kk gibi, belirli bir deere kadar blnmesidir."} />
        <TermCard term={"Matematiksel Modelleme"} definition={"Gerek hayattaki durumlar matematiksel ifadelerle temsil etme ilemidir."} />
        <TermCard term={"Saylar Teorisi"} definition={"Saylar teorisi, tam saylar ve bunlarn zelliklerini inceleyen bir matematik daldr."} />
        <TermCard term={"statistik"} definition={"statistik, veri toplama, analiz etme ve yorumlama bilimi olarak tanmlanr."} />
        <TermCard term={"Permtasyon"} definition={"Bir kmedeki elemanlarn farkl sralamalarn inceleyen matematiksel bir kavramdr."} />
        <TermCard term={"Kombinasyon"} definition={"Bir kmeden belirli sayda elemann seilmesi durumunda oluan gruplardr."} />
            </div>
        </div>
      </main>
    </div>
  );
};

export default Home;
