import React, { useState } from 'react';

const CircleCalculator = () => {
  const [cap, setCap] = useState(''); // Dairenin ap
  const [alan, setAlan] = useState(''); // Dairenin alan
  const [cevre, setCevre] = useState(''); // Dairenin evresi

  // ap inputuna sadece say giriine izin veren fonksiyon
  const handleNumberInput = (setter) => (e) => {
    const value = e.target.value.replace(/[^0-9]/g, ''); // Sadece rakamlara izin verir
    setter(value);
  };

  // Dairenin alann hesaplama fonksiyonu
  const handleCalculateArea = () => {
    if (!isNaN(cap) && cap !== '') {
      const radius = parseFloat(cap) / 2; // aptan yarap bul
      const alanSonuc = Math.PI * Math.pow(radius, 2); // Alan: r
      setAlan(alanSonuc.toFixed(2)); // Sonucu iki ondalk basamakla gster
    } else {
      setAlan('Hata');
    }
  };

  // Dairenin evresini hesaplama fonksiyonu
  const handleCalculatePerimeter = () => {
    if (!isNaN(cap) && cap !== '') {
      const cevreSonuc = Math.PI * parseFloat(cap); // evre: d
      setCevre(cevreSonuc.toFixed(2)); // Sonucu iki ondalk basamakla gster
    } else {
      setCevre('Hata');
    }
  };

  // Temizleme fonksiyonu
  const handleClear = () => {
    setCap('');
    setAlan('');
    setCevre('');
  };

  // Butonlarn devre d olup olmayacan kontrol eden deiken
  const isDisabled = cap === '';

  return (
    <div className="flex items-center justify-center bg-[#6c757d]">
      <div className="bg-[#6c757d] rounded-lg p-6 w-full max-w-sm">
        <div className="mb-4">
          <div className="flex flex-col items-center">
            {/* ap */}
            <label className="text-white">Dairenin ap:</label>
            <input
              type="text"
              value={cap}
              onChange={handleNumberInput(setCap)}
              className="p-2 text-black border rounded-md w-full md:w-40"
              placeholder="Sadece rakam girin"
            />
          </div>
        </div>

        {/* Alan ve evre Hesaplama */}
        <div className="mb-4">
          <div className="flex justify-between">
            <button
              onClick={handleCalculateArea}
              className="bg-white text-[#6c757d] rounded-lg p-2 hover:bg-[#6c757d] hover:text-white"
              disabled={isDisabled} // Deer girilmediyse buton devre d
            >
              Alan Hesapla
            </button>
            <div className="text-white font-semibold">Alan: {alan}</div>
          </div>

          <hr className="my-4 border-white" />

          <div className="flex justify-between">
            <button
              onClick={handleCalculatePerimeter}
              className="bg-white text-[#6c757d] rounded-lg p-2 hover:bg-[#6c757d] hover:text-white"
              disabled={isDisabled} // Deer girilmediyse buton devre d
            >
              evreyi Hesapla
            </button>
            <div className="text-white font-semibold">evre: {cevre}</div>
          </div>
        </div>

        {/* Temizle Butonu */}
        <button
          className="bg-black text-white rounded-lg p-2 hover:bg-white hover:text-black w-full mt-4"
          onClick={handleClear}
        >
          Temizle
        </button>
      </div>
    </div>
  );
};

export default CircleCalculator;
