import React, { useState } from 'react';

const SquareCalculator = () => {
  const [kenarUzunlugu, setKenarUzunlugu] = useState('');
  const [alan, setAlan] = useState('');
  const [cevre, setCevre] = useState('');

  // Kenar uzunluu inputuna sadece say giriine izin veren fonksiyon
  const handleNumberInput = (setter) => (e) => {
    const value = e.target.value.replace(/[^0-9]/g, ''); // Sadece rakamlara izin verir
    setter(value);
  };

  // Karenin alann hesaplama fonksiyonu
  const handleCalculateArea = () => {
    if (!isNaN(kenarUzunlugu) && kenarUzunlugu !== '') {
      const alanSonuc = parseFloat(kenarUzunlugu) ** 2; // Kenarn karesi
      setAlan(alanSonuc);
    } else {
      setAlan('Hata');
    }
  };

  // Karenin evresini hesaplama fonksiyonu
  const handleCalculatePerimeter = () => {
    if (!isNaN(kenarUzunlugu) && kenarUzunlugu !== '') {
      const cevreSonuc = 4 * parseFloat(kenarUzunlugu); // 4 * kenar uzunluu
      setCevre(cevreSonuc);
    } else {
      setCevre('Hata');
    }
  };

  // Temizleme fonksiyonu
  const handleClear = () => {
    setKenarUzunlugu('');
    setAlan('');
    setCevre('');
  };

  // Butonlarn devre d olup olmayacan kontrol eden deiken
  const isDisabled = kenarUzunlugu === '';

  return (
    <div className="flex items-center justify-center bg-[#6c757d]">
      <div className="bg-[#6c757d] rounded-lg p-6 w-full max-w-sm">
        <div className="mb-4">
          <div className="flex flex-col items-center">
            {/* Kenar Uzunluu */}
            <label className="text-white">Kenar Uzunluu:</label>
            <input
              type="text"
              value={kenarUzunlugu}
              onChange={handleNumberInput(setKenarUzunlugu)}
              className="p-2 text-black border rounded-md w-full md:w-40"
              placeholder="Sadece rakam girin"
            />
          </div>
        </div>

        {/* Alan ve evre Hesaplama */}
        <div className="mb-4">
          <div className="flex justify-between">
            <button
              onClick={handleCalculateArea}
              className="bg-white text-[#6c757d] rounded-lg p-2 hover:bg-[#6c757d] hover:text-white"
              disabled={isDisabled} // Deer girilmediyse buton devre d
            >
              Alan Hesapla
            </button>
            <div className="text-white font-semibold">Alan: {alan}</div>
          </div>

          <hr className="my-4 border-white" />

          <div className="flex justify-between">
            <button
              onClick={handleCalculatePerimeter}
              className="bg-white text-[#6c757d] rounded-lg p-2 hover:bg-[#6c757d] hover:text-white"
              disabled={isDisabled} // Deer girilmediyse buton devre d
            >
              evreyi Hesapla
            </button>
            <div className="text-white font-semibold">evre: {cevre}</div>
          </div>
        </div>

        {/* Temizle Butonu */}
        <button
          className="bg-black text-white rounded-lg p-2 hover:bg-white hover:text-black w-full mt-4"
          onClick={handleClear}
        >
          Temizle
        </button>
      </div>
    </div>
  );
};

export default SquareCalculator;
